	module.exports = {"data":{"markdownRemark":{"html":"<p>セメント工場をバックに描いた水彩画。</p>\n<p>美術の授業にて、いつものノリで描いていた作品が、なぜか<em>校内展銀賞 + 地区展銀賞</em>というなかなか凄い結果を出してしまった作品である。\nちなみに美術作品として(ファンイラストとか除いて)入選したのは、これが最初で最後である。</p>\n<p><a href=\"https://www.google.co.jp/maps/@35.940711,139.337592,3a,75y,34.79h,95.36t/data=!3m4!1e1!3m2!1s3TfJl9DgCF2KcOAbpBQcKA!2e0\">写生場所は恐らくこの辺</a>。\n奥にあったはずのセメント工場は既に解体され、現状資材置き場と化していることもあり、現在の風景とはだいぶ異なっている。</p>","frontmatter":{"cover":null,"date":"1992-10-12T14:00:06+09:00","strDate":"1992/10/12","title":"『秋の風景』"}}},"pathContext":{"path":"/ja/blog/1992/10/12-autumn/","langKey":"ja"}}

